
.imgContent{
  background: #FFF;
  border-color: hsla(0, 0%, 0%, 0.5);
  border-radius: 5px;
  border: 2px solid #333;
}
.imgContent:hover{
  cursor: pointer;
}
.square{
  width: 160px;
  height: 160px;
}
img{
  max-width: 100%;
  max-height:100%;
}
/* IF YOU NEED HELP UNCOMMENT */
.imgContent:hover > h3{
  position:absolute;
  display:inherit;
  bottom: -100px;
  color:black;
  margin: 35% 35%;
  text-align: center;
}
h3.helper{
  display:none;
}